import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import styles from "./about-page-content.module.scss"
import EmptyLine from "./empty-line"
import PageHeader from "./page-header"
import avatarImage from "../images/avatar.png"
import toolPsImage from "../images/toolPs.png"
import toolFigmaImage from "../images/toolFigma.png"
import toolAiImage from "../images/toolAi.png"
import toolAeImage from "../images/toolAe.png"

export default () => {
  const { travelMyWorld, raiseCats, createThings } = useStaticQuery(graphql`
    query {
      travelMyWorld: file(relativePath: { eq: "travelMyWorld.png" }) {
        childImageSharp {
          fixed(height: 228) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      raiseCats: file(relativePath: { eq: "raiseCats.png" }) {
        childImageSharp {
          fixed(height: 228) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      createThings: file(relativePath: { eq: "createThings.png" }) {
        childImageSharp {
          fixed(height: 228) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <PageHeader title="About Me" />
      <EmptyLine />
      <div className="container">
        <div className="columns is-gapless">
          <div className={`column is-narrow ${styles.avatarWrapper}`}>
            <img src={avatarImage} alt="Avatar" className={styles.avatar} />
          </div>
          <div className="column">
            <div className={styles.heading}>Hello! I’m Hannah Nguyen.</div>
            <div className={styles.text}>
              <div>
                I’m an ordinary girl striving to add every little&nbsp;
                <strong>extra</strong> to become <strong>extraordinary</strong>.
              </div>
              <EmptyLine variant="Paragraph" />
              <div>
                When I fall into an unfavorable situation, the first thing comes
                to my mind is "
                <strong>
                  How to get out of this as soon and efficiently as possible?
                </strong>
                ", instead of "<strike>Why me</strike>?".
              </div>
            </div>
          </div>
        </div>
        <EmptyLine variant="Paragraph" />
        <div className={styles.heading}>I design web and mobile.</div>
        <div className={styles.text}>
          I’m passionate about&nbsp;<strong>UI/UX design</strong>. Not aiming to
          be just a pixel pusher, I attempt to become an expert in web and
          mobile design.
        </div>
        <EmptyLine variant="Paragraph" />
        <div className={styles.proficiencyContainer}>
          <div className={`${styles.text} ${styles.proficiencyLabel}`}>
            My proficiency:
          </div>
          <img src={toolPsImage} alt="PS" className={styles.proficiencyTool} />
          <img
            src={toolFigmaImage}
            alt="Figma"
            className={styles.proficiencyTool}
          />
          <img src={toolAiImage} alt="AI" className={styles.proficiencyTool} />
          <img src={toolAeImage} alt="AE" className={styles.proficiencyTool} />
        </div>
        <EmptyLine />
        <div className="columns is-gapless">
          <div className="column has-text-centered has-text-left-desktop">
            <div className={styles.heading}>I travel my world.</div>
            <EmptyLine variant="Paragraph" />
            <Img fixed={travelMyWorld.childImageSharp.fixed} alt="" />
            <EmptyLine />
          </div>
          <div className="column has-text-centered">
            <div className={styles.heading}>I raise cats.</div>
            <EmptyLine variant="Paragraph" />
            <Img fixed={raiseCats.childImageSharp.fixed} alt="" />
            <EmptyLine />
          </div>
          <div className="column has-text-centered has-text-right-desktop">
            <div className={styles.heading}>I create things.</div>
            <Img fixed={createThings.childImageSharp.fixed} alt="" />
            <EmptyLine />
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}
