import React from "react"
import PropTypes from "prop-types"

import styles from "./page-header.module.scss"
import pineImage from "../images/pine.svg"

const PageHeader = ({ title, hasBackgroundColor }) => {
  return (
    <div className={`${hasBackgroundColor ? styles.backgroundColor : ""}`}>
      <div className={`container ${styles.mainContent}`}>
        <img src={pineImage} alt="" className={styles.pine} />
        <div className={styles.title}>{title}</div>
        <img
          src={pineImage}
          alt=""
          className={`${styles.pine} ${styles.flip}`}
        />
      </div>
    </div>
  )
}

PageHeader.propTypes = {
  title: PropTypes.string.isRequired,
}

export default PageHeader
