import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import styles from "./contact-page-content.module.scss"
import treeWithLightImage from "../images/treeWithLight.png"
import pineImage from "../images/pine.svg"
import catFaceImage from "../images/cat-face.png"
import EmptyLine from "./empty-line"

export default () => {
  const { iconPhone, iconMail, iconSkype } = useStaticQuery(graphql`
    query {
      iconPhone: file(relativePath: { eq: "iconPhone.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconMail: file(relativePath: { eq: "iconMail.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconSkype: file(relativePath: { eq: "iconSkype.png" }) {
        childImageSharp {
          fixed(height: 40) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <div className="hero is-fullheight-with-navbar">
      <div className={styles.mainContent}>
        <div className="container">
          <EmptyLine />
          <div className="columns is-gapless is-mobile">
            <div className="column is-narrow">
              <img src={pineImage} alt="" className={styles.pine} />
            </div>
            <div className={`column ${styles.headingContainer}`}>
              <div>Let's Make</div>
              <div>Something Great</div>
              <div>Together</div>
            </div>
            <div className="column is-narrow">
              <img
                src={pineImage}
                alt=""
                className={`${styles.pine} ${styles.flip}`}
              />
            </div>
          </div>
          <div className="columns is-gapless">
            <div className={`column ${styles.contactInfo}`}>
              <EmptyLine variant="Paragraph" />
              <Img fixed={iconPhone.childImageSharp.fixed} alt="" />
              <div className={styles.contactInfoText}>(+84)962.61.81.22</div>
              <EmptyLine variant="Paragraph" />
            </div>
            <div className={`column ${styles.contactInfo}`}>
              <EmptyLine variant="Paragraph" />
              <Img fixed={iconSkype.childImageSharp.fixed} alt="" />
              <div className={styles.contactInfoText}>hanguyen459</div>
              <EmptyLine variant="Paragraph" />
            </div>
            <div className={`column ${styles.contactInfo}`}>
              <EmptyLine variant="Paragraph" />
              <Img fixed={iconMail.childImageSharp.fixed} alt="" />
              <div className={styles.contactInfoText}>ntbichha95@gmail.com</div>
              <EmptyLine variant="Paragraph" />
            </div>
          </div>

          <div className={styles.noticeSection}>
            <div className={styles.noticeText}>
              Please feel free. I don't scratch.
            </div>
            <img src={catFaceImage} alt="" className={styles.catFaceImage} />
          </div>
          <EmptyLine variant="Paragraph" />
        </div>
      </div>
      <div className={`container ${styles.treeSection}`}>
        <img src={treeWithLightImage} alt="" className={styles.treeImage} />
      </div>
    </div>
  )
}
