import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import AniLink from "gatsby-plugin-transition-link/AniLink"

import styles from "./my-work-page-content.module.scss"
import ButtonLink from "./button-link"
import EmptyLine from "./empty-line"
import PageHeader from "./page-header"

export default () => {
  const {
    mockupDreamWork,
    mockupHippoMeowV1,
    mockupHippoMeowV2,
    mockupMicroLector,
    mockupTitanNews,
    iconArrowGreen,
    iconArrowYellow,
  } = useStaticQuery(graphql`
    query {
      mockupDreamWork: file(relativePath: { eq: "mockupDreamWork.png" }) {
        childImageSharp {
          fluid(maxWidth: 544) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      mockupHippoMeowV1: file(relativePath: { eq: "mockupHippoMeowV1.png" }) {
        childImageSharp {
          fluid(maxWidth: 544) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      mockupHippoMeowV2: file(relativePath: { eq: "mockupHippoMeowV2.png" }) {
        childImageSharp {
          fluid(maxWidth: 544) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      mockupMicroLector: file(relativePath: { eq: "mockupMicroLector.png" }) {
        childImageSharp {
          fluid(maxWidth: 544) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      mockupTitanNews: file(relativePath: { eq: "mockupTitanNews.png" }) {
        childImageSharp {
          fluid(maxWidth: 544) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      iconArrowGreen: file(relativePath: { eq: "iconArrowGreen.png" }) {
        childImageSharp {
          fixed(width: 60) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      iconArrowYellow: file(relativePath: { eq: "iconArrowYellow.png" }) {
        childImageSharp {
          fixed(width: 60) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  const projects = [
    {
      wrapperClassName: styles.backgroundWhite,
      mockupImage: mockupDreamWork.childImageSharp.fluid,
      message:
        "I create a website for posting and seeking jobs in design field. The main color is used based on 2020 color trend: classic blue. This color certainly brings a feeling of trust and gentleness.",
      to: "/my-work/dreamwork",
      arrowImage: iconArrowGreen.childImageSharp.fixed,
      linkClassName: "",
    },
    {
      wrapperClassName: styles.backgroundYellow,
      mockupImage: mockupTitanNews.childImageSharp.fluid,
      message:
        "This is the homepage of an online newspaper. It is a big challenge for alignment and content arrangement.",
      to: "/my-work/titannews",
      arrowImage: iconArrowGreen.childImageSharp.fixed,
      linkClassName: "",
    },
    {
      wrapperClassName: styles.backgroundGreen,
      mockupImage: mockupMicroLector.childImageSharp.fluid,
      message:
        "I redesign an existing education websign homepage. Service logos and details, as well as attractive information, have been added.",
      to: "/my-work/microlector",
      arrowImage: iconArrowYellow.childImageSharp.fixed,
      linkClassName: styles.projectCtaLinkTextYellow,
    },
    {
      wrapperClassName: styles.backgroundWhite,
      mockupImage: mockupHippoMeowV1.childImageSharp.fluid,
      message:
        "I work on an idea for a family cake and cafe shop website. I aim to bring a fresh and friendly atmosphere for the homepage.",
      to: "/my-work/hippo-meow-v1",
      arrowImage: iconArrowGreen.childImageSharp.fixed,
      linkClassName: "",
    },
    {
      wrapperClassName: styles.backgroundYellow,
      mockupImage: mockupHippoMeowV2.childImageSharp.fluid,
      message:
        "I want to bring a traditional and cozy atmosphere for the users, so I let the homepage imitate the interface of a shop in reality with wood walls, lean-to, neon signboard, and window.",
      to: "/my-work/hippo-meow-v2",
      arrowImage: iconArrowGreen.childImageSharp.fixed,
      linkClassName: "",
    },
  ]

  const renderProject = (project, projectIndex) => {
    const {
      wrapperClassName,
      mockupImage,
      message,
      to,
      arrowImage,
      linkClassName,
    } = project
    return (
      <div key={projectIndex} className={wrapperClassName}>
        <div className="container">
          <div
            className={`columns is-gapless ${styles.project} ${
              projectIndex % 2 === 0 ? styles.columnReverseForDesktop : ""
            }`}
          >
            <div className="column">
              <Img fluid={mockupImage} alt="" />
            </div>
            <div
              className={`column is-narrow ${styles.projectInfoSeparator}`}
            />
            <div className={`column ${styles.projectInfo}`}>
              <div>{message}</div>
              <EmptyLine />
              <AniLink fade className={styles.projectCtaLink} to={to}>
                <Img fixed={arrowImage} alt="" />
                <span
                  className={`${styles.projectCtaLinkText} ${linkClassName}`}
                >
                  More Details
                </span>
              </AniLink>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <React.Fragment>
      <PageHeader title="My Work" hasBackgroundColor />
      {projects.map(renderProject)}
      <EmptyLine />
      <div className="container">
        <div className={styles.moreWorksSectionContent}>
          <div className={styles.moreWorksGreenLine} />
          <div className={styles.moreWorksButtonContainer}>
            <ButtonLink to="/404" title="More Works" />
          </div>
          <div className={styles.moreWorksGreenLine} />
        </div>
      </div>
      <EmptyLine />
    </React.Fragment>
  )
}
